(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1454570327559;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .factory('FoursquareVenuesService', FoursquareVenuesService);

  /** @ngInject */
  function FoursquareVenuesService(httpqService) {
    var service = {
      getAllVenues: getAllVenues,
      getVenue: getVenue
    }

    return service;

    function getAllVenues(offset) {
      return httpqService.get('venues?offset='+offset);
    }

    function getVenue(venueId) {
      return httpqService.get('venue/'+venueId);
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .controller('FoursquareVenuesController', FoursquareVenuesController);

  /** @ngInject */
  function FoursquareVenuesController(FoursquareVenuesService, $log) {
    var vm = this;
    var limit = 50;
    var offset = 0;
    vm.next = next;
    vm.back = back;
    getVenues(offset);

    function getVenues(offset) {
      return FoursquareVenuesService.getAllVenues(offset)
        .then(function(data) {
          $log.info(data);
          return vm.items = data.response.groups[0].items;
        })
        .catch(function(err) {
          $log.error(err);
        });
    }

    function next() {
      offset += limit;
      return getVenues(offset);
    }

    function back() {
      offset -=limit;
      if (offset<0) {
        offset = 0;
      }
      return getVenues(offset);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('foursquarevenues')
    .directive('venueHours', venueHours);

  /** @ngInject */
  function venueHours() {
    var directive = {
      restrict: 'E',
      template: '<p ng-repeat="hour in vm.hours track by $index">{{hour}}<p>',
      scope: {
        venueId: '@'
      },
      controller: VenueHoursController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function VenueHoursController(FoursquareVenuesService, $log) {
      var vm = this;
      var hours = [];
      var hoursObj ={};

      FoursquareVenuesService.getVenue(vm.venueId)
        .then(function(data) {
          $log.info(data);
          if (typeof data.response.venue.hours.timeframes != "undefined") {
            var timeframes = data.response.venue.hours.timeframes
            angular.forEach(timeframes, function(timeframe){
              var daysOpen = timeframe.days;
              var open = timeframe.open[0].renderedTime;
              hoursObj[daysOpen] = open;
              hours.push(hoursObj);
            })
            vm.hours = hours;
          }
        })
        .catch(function(err) {
          $log.error(err);
        })
    }
  }

})();

'use strict';

angular.module('foursquarevenues')
  .factory('httpqService', function($http, $q, serverUrl) {
    var factory = {};
    factory.getApiUrl = function(resource) {
      return (serverUrl || '') + resource;
    };
    factory.get = function(resource) {
      var defer = $q.defer();
      $http.get(factory.getApiUrl(resource)).success(function(data) {
        defer.resolve(data);
      }).error(function(error) {
        defer.reject(error);
      });
      return defer.promise;
    };
    factory.post = function(resource, payload) {
      var defer = $q.defer();
      $http.post(factory.getApiUrl(resource), payload).success(function(data) {
        defer.resolve(data);
      }).error(function(error) {
        defer.reject(error);
      });
      return defer.promise;
    };
    factory.put = function(resource, payload) {
      var defer = $q.defer();
      $http.put(factory.getApiUrl(resource), payload).success(function(data) {
        defer.resolve(data);
      }).error(function(error) {
        defer.reject(error);
      });
      return defer.promise;
    };
    return factory;
  });

